

// Creates view model for the main menu.

function menu(params) {

    var model = {

        active: params.active
    };

    return model;
}

ko.components.register('bc-menu', {

    viewModel: { createViewModel: menu },

    template: "<!-- ko if: active() -->\n    <div class=\"btn-group\" id=\"menu\">\n        <a data-bind=\"css: { active: active() === 'posts' }\" class=\"btn btn-default\" href=\"#entries/post\">Posts</a>\n        <a data-bind=\"css: { active: active() === 'pages' }\" class=\"btn btn-default\" href=\"#entries/page\">Pages</a>\n        <a data-bind=\"css: { active: active() === 'blocks' }\" class=\"btn btn-default\" href=\"#entries/block\">Blocks</a>\n        <a data-bind=\"css: { active: active() === 'users' }\" class=\"btn btn-default\" href=\"#users\">Users</a>\n        <a data-bind=\"css: { active: active() === 'files' }\" class=\"btn btn-default\" href=\"#files\">Files</a>\n        <a class=\"btn btn-default\" href=\"#logout\">Logout</a>\n    </div>\n<!-- /ko -->\n"
});
