
var api = require('../api');
var message = require('../message');
var posts_item = require('../vm/posts_item');

// Component for displaying a post list.

function page(params) {

    var type = params.type;

    var model = {

        type: type,

        all: ko.observable([]),

        count: ko.observable(5),

        step: ko.observable(5)
    };

    // Shows whether there are more
    // pager pages.

    model.hasMore = ko.pureComputed(function() {

        return model.count() < model.all().length;
    });

    // Shows new pager page with posts.

    model.showMore = function() {

        model.count(model.count() + model.step());

        setTimeout(function() {

            // Scrolls to the bottom.

            window.scrollTo(0, document.body.scrollHeight);

        }, 50);
    };

    // Shows all posts.

    model.showAll = function() {

        model.count(model.all().length);
    };

    // Posts array considering the current
    // pager state.

    model.posts = ko.pureComputed(function() {

        var all = model.all();

        return all.slice(0, model.count());
    });

    var tasks = [ api.userInfo(), api.posts(type) ];

    Promise.all(tasks).then(function(data) {

        var info = data[0], posts = data[1];

        posts.sort(function(post1, post2) {

            return post2.date_updated - post1.date_updated;
        });

        model.all(posts.map(function(data) {

            var post = posts_item.create(data);

            post.editable = info.type === 'admin' || info.$id === post.author;

            return post;

        }));

    }).catch(message.error);

    return model;
}

ko.components.register('posts', {

    viewModel: { createViewModel: page },

    template: "<h2>Posts</h2>\n\n<ul data-bind=\"foreach: posts\" class=\"list-group\">\n    <li class=\"list-group-item post-item\">\n        <h4 data-bind=\"click: expand, text: title\" class=\"list-group-item-heading\"></h4>\n        <div class=\"list-group-item-text\">\n            Update date: <span data-bind=\"text: formatDate(date_updated)\"></span>,\n            comments: <span data-bind=\"text: comments\"></span>\n            <div data-bind=\"visible: expanded\" class=\"post-controls\">\n                <a data-bind=\"attr: { href: '#entry/' + type + '/' + $id }, css: { disabled: !editable }\"\n                    class=\"btn btn-default btn-sm\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-edit\"></span> Edit\n                </a>\n                <a data-bind=\"attr: { href: '#comments/' + type + '/' + $id }, visible: type === 'post', css: { disabled: comments === 0 || !editable }\"\n                    class=\"btn btn-default btn-sm\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-comment\"></span> Comments\n                </a>\n                <a data-bind=\"click: remove, css: { disabled: !editable }\"\n                    class=\"btn btn-default btn-sm\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-edit\"></span> Remove\n                </a>\n            </div>\n        </div>\n    </li>\n</ul>\n\n<a data-bind=\"click: showMore, css: { disabled: !hasMore() }\" class=\"btn btn-default\" href=\"#\">\n    <span class=\"glyphicon glyphicon-list-alt\"></span> Show more\n</a>\n<a data-bind=\"click: showAll, css: { disabled: !hasMore() }\" class=\"btn btn-default\" href=\"#\">\n    <span class=\"glyphicon glyphicon-list-alt\"></span> Show all\n</a>\n<a data-bind=\"attr: { href: '#new/' + type }\" class=\"btn btn-default\" href=\"#\">\n    <span class=\"glyphicon glyphicon-plus\"></span> Add\n</a>\n"
});
