
var api = require('../api');
var message = require('../message');
var comments_item = require('../vm/comments_item');

// Creates view model for the comments list.

function page(params) {

    var model = {

        title: ko.observable(),

        comments: ko.observable([])
    };

    var tasks = [ api.entryInfo(params.id), api.comments(params.id) ];

    Promise.all(tasks).then(function(data) {

        var info = data[0], comments = data[1];

        model.title(info.title);

        model.comments(comments.map(function(data) {

            return comments_item.create(data);

        }));

    }).catch(message.error);

    return model;
}

ko.components.register('comments', {

    viewModel: { createViewModel: page },

    template: "<h2 data-bind=\"text: title\"></h2>\n<h3>Comments</h3>\n<ul data-bind=\"foreach: comments\" class=\"list-group\">\n    <li class=\"list-group-item post-item\">\n        <h4 data-bind=\"text: author, click: expand\" class=\"list-group-item-heading\"></h4>\n        <div class=\"list-group-item-text\">\n            Date: <span data-bind=\"text: formatDate(date)\"></span>\n            <div data-bind=\"visible: expanded\" class=\"post-controls\">\n                <div class=\"panel panel-default\">\n                    <div data-bind=\"text: message\" class=\"panel-body\"></div>\n                </div>\n                <a data-bind=\"click: remove\" class=\"btn btn-default btn-sm\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-remove\"></span> Remove\n                </a>\n            </div>\n        </div>\n    </li>\n</ul>\n"
});
