
var api = require('../api');
var path = require('../path');
var message = require('../message');

// Component for displaying a directory.

function page(params) {

    var file = params.file;

    var model = {

        file: path.decode(file),

        parent: path.parent(file),

        size: ko.observable(),

        date: ko.observable(0)
    };

    model.remove = function() {

        if (confirm('Remove file ' + path.decode(file) + '?')) {

            api.removeFile(file).then(function() {

                message.info('File "' + path.decode(file) + '" removed.');

                route.go('directory/' + model.parent);

            }).catch(message.error);
        }
    };

    api.file(file).then(function(data) {

        model.size(data.size);

        model.date(data.modified);

    }).catch(message.error);

    return model;
}

ko.components.register('file', {

    viewModel: { createViewModel: page },

    template: "<h2 data-bind=\"text: 'File ' + file\"></h2>\n<p>\n    File size: <span data-bind=\"text: size\"></span> bytes<br>\n    Last uploaded: <span data-bind=\"text: formatDate(date())\"></span>\n</p>\n<p>\n    <a data-bind=\"attr: { href: '#directory/' + parent }\" class=\"btn btn-default\" href=\"#\">\n        <span class=\"glyphicon glyphicon-arrow-up\"></span> Up\n    </a>\n    <a data-bind=\"attr: { href: file }\" href=\"#\" target=\"_blank\" class=\"btn btn-default\">\n        Direct link (opens in new tab)\n    </a>\n    <a data-bind=\"click: remove\" class=\"btn btn-default\" href=\"#\">\n        <span class=\"glyphicon glyphicon-remove-circle\"></span> Remove\n    </a>\n</p>\n"
});
