
var api = require('../api');
var focus = require('../focus');
var user = require('../vm/user');
var message = require('../message');

// Component for user edit page.

function page(params) {

    var model = {

        user: ko.observable()
    };

    if (params.id) {

        // Edit existing user.

        var tasks = [ api.userInfo(), api.user(params.id) ];

        Promise.all(tasks).then(function(data) {

            var info = data[0], userData = data[1];

            model.user(user.create(info, userData));

            focus('#user-fullname');

        }).catch(message.error);

    } else {

        // Create a new user.

        api.userInfo().then(function(info) {

            model.user(user.create(info));

            focus('#user-fullname');

        }).catch(message.error);
    }

    return model;
}

ko.components.register('user', {

    viewModel: { createViewModel: page },

    template: "<!-- ko with: user -->\n    <h2 data-bind=\"text: fullname\"></h2>\n\n    <form data-bind=\"submit: save\">\n        <div class=\"form-group\">\n            <label for=\"user-fullname\">Full name</label>\n            <input data-bind=\"value: fullname\"\n                type=\"text\" class=\"form-control\" id=\"user-fullname\"\n                placeholder=\"Full name\">\n        </div>\n        <div class=\"form-group\">\n            <label for=\"user-username\">Username</label>\n            <input data-bind=\"value: username\"\n                type=\"text\" class=\"form-control\" id=\"user-username\"\n                placeholder=\"Username\">\n        </div>\n        <div class=\"form-group\">\n            <label for=\"user-type\">Type</label>\n            <select data-bind=\"value: type\" class=\"form-control\">\n                <option value=\"author\">Author</option>\n                <option value=\"admin\">Admin</option>\n            </select>\n        </div>\n        <div class=\"form-group\">\n            <label for=\"user-link\">Link</label>\n            <input data-bind=\"value: link\"\n                type=\"text\" class=\"form-control\" id=\"user-link\"\n                placeholder=\"Link\">\n        </div>\n        <div class=\"checkbox\">\n            <label><input data-bind=\"checked: files, disable: my_type !== 'admin'\" type=\"checkbox\">\n                Allow file management</label>\n        </div>\n        <div class=\"checkbox\">\n            <label><input data-bind=\"checked: password_edit, disable: creating\" type=\"checkbox\">\n                Edit password</label>\n        </div>\n        <div data-bind=\"visible: password_edit\" class=\"form-group\">\n            <label for=\"user-password\">New password</label>\n            <input data-bind=\"value: password\"\n                type=\"password\" class=\"form-control\" id=\"user-password\"\n                placeholder=\"Password\">\n            <div class=\"checkbox\">\n                <label><input data-bind=\"checked: password_text\" type=\"checkbox\">\n                    Show text</label>\n            </div>\n        </div>\n        <button type=\"submit\" class=\"btn btn-default\">\n            <span class=\"glyphicon glyphicon-ok\"></span> Save\n        </button>\n        <a class=\"btn btn-default\" href=\"#users\">\n            <span class=\"glyphicon glyphicon-arrow-left\"></span> Cancel\n        </a>\n    </form>\n<!-- /ko -->\n"
});
