
var api = require('../api');
var path = require('../path');
var message = require('../message');
var directory_item = require('../vm/directory_item');

// Component for displaying a directory.

function page(params) {

    var directory = params.directory;

    var model = {

        directory: path.decode(directory),

        entries: ko.observable([]),

        parent: path.parent(directory),

        subdirectory: {

            form: ko.observable(false),

            directory: ko.observable('').trimmed()
        },

        upload: {

            form: ko.observable(false),

            progress: ko.observable(0)
        }
    };

    // Opens subdirectory form.

    model.subdirectory.show = function() {

        model.subdirectory.form(true);
    };

    // Creates the subdirectory.

    model.subdirectory.submit = function() {

        var sub = model.subdirectory.directory();

        api.createDirectory(directory, sub).then(function() {

            message.info('Directory "' + path.decode(path.join(directory, sub)) +
                '" has been created.');

            model.subdirectory.form(false);

            route.go('directory/' + path.join(directory, sub));

        }).catch(message.error);
    };

    // Cancels and hides the subdirectory form.
    // Resets the form.

    model.subdirectory.cancel = function() {

        model.subdirectory.form(false);
        model.subdirectory.directory('');
    };

    // Shows file upload form.

    model.upload.show = function() {

        model.upload.form(true);
    };

    // Submits the upload form and
    // performs the file upload process.

    model.upload.submit = function() {

        var file = document.getElementById('directory-file').files[0];

        if (!file) {

            return;
        }

        model.upload.file = file;

        var reader = new FileReader();

        reader.onload = function() {

            var xhr = new XMLHttpRequest();

            xhr.upload.addEventListener('progress', model.upload.progress, false);

            xhr.addEventListener('load', model.upload.complete, false);
            xhr.addEventListener('error', model.upload.failed, false);
            xhr.addEventListener('abort', model.upload.aborted, false);

            xhr.open('POST', '/api/upload/' + encodeURIComponent(directory));

            xhr.setRequestHeader('X-Key', api.apiKey());
            xhr.setRequestHeader('X-File-Name', file.name);
            xhr.setRequestHeader('Content-Type', 'application/octet-stream');

            xhr.send(new Uint8Array(reader.result));
        };

        reader.readAsArrayBuffer(file);
    };

    // Cancels and hides the upload form.

    model.upload.cancel = function() {

        model.upload.form(false);
    };

    // Reports the upload progress.
    // FIXME only works on Firefox?

    model.upload.progress = function(e) {

        if (e.lengthComputable) {

            model.upload.progress(Math.round(e.loaded * 100 / e.total));
        }
    };

    // Executed then the upload process
    // is complete.

    model.upload.complete = function() {

        route.refresh();

        message.info('File "' + path.decode(path.join(directory, model.upload.file.name)) +
            '" has been uploaded.');
    };

    model.upload.failed = function() {

        // FIXME needs error checking.
    };

    // Executed when the upload process
    // was aborted.

    model.upload.aborted = function() {

        model.upload.form(false);
    };

    model.remove = function() {

        if (confirm('Remove directory "' + path.decode(directory) + '"?')) {

            api.removeDirectory(directory).then(function() {

                message.info('Directory "' + path.decode(directory) +
                    '" has been successfully removed.');

                route.go('directory/' + model.parent);

            }).catch(message.error);
        }
    };

    api.directory(directory).then(function(list) {

        var entries = list.map(function(data) {

            return directory_item.create(directory, data);

        });

        entries.sort(directory_item.compare);

        model.entries(entries);

    }).catch(message.error);

    return model;
}

ko.components.register('directory', {

    viewModel: { createViewModel: page },

    template: "<h2 data-bind=\"text: 'Directory ' + directory\"></h2>\n\n<p>\n    <!-- ko if: directory !== '/' -->\n        <a data-bind=\"attr: { href: '#directory/' + parent }\"\n            class=\"btn btn-default\" href=\"#\">\n            <span class=\"glyphicon glyphicon-arrow-up\"></span> Up\n        </a>\n    <!-- /ko -->\n    <a data-bind=\"click: subdirectory.show\" class=\"btn btn-default\" href=\"#\">\n        <span class=\"glyphicon glyphicon-folder-close\"></span> Add\n    </a>\n    <a data-bind=\"click: upload.show\" class=\"btn btn-default\" href=\"#\">\n        <span class=\"glyphicon glyphicon-upload\"></span> Upload\n    </a>\n    <!-- ko if: directory !== '/' -->\n        <a data-bind=\"click: remove\"\n            class=\"btn btn-default\" href=\"#\">\n            <span class=\"glyphicon glyphicon-remove-circle\"></span> Remove\n        </a>\n    <!-- /ko -->\n</p>\n\n<!-- ko if: subdirectory.form() -->\n    <div class=\"panel panel-default\">\n        <div class=\"panel-heading\">New directory</div>\n        <div class=\"panel-body\">\n            <form data-bind=\"submit: subdirectory.submit\">\n                <div class=\"form-group\">\n                    <label for=\"directory-new\">Directory name</label>\n                    <input data-bind=\"value: subdirectory.directory\"\n                        type=\"text\" class=\"form-control\" id=\"directory-new\"\n                        placeholder=\"Directory name\" required=\"true\">\n                </div>\n                <button type=\"submit\" class=\"btn btn-default\">\n                    <span class=\"glyphicon glyphicon-folder-close\"></span> Add\n                </button>\n                <a data-bind=\"click: subdirectory.cancel\" class=\"btn btn-default\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-remove-circle\"></span> Cancel\n                </a>\n            </form>\n        </div>\n    </div>\n<!-- /ko -->\n\n<!-- ko if: upload.form() -->\n    <div class=\"panel panel-default\">\n        <div class=\"panel-heading\">Upload file</div>\n        <div class=\"panel-body\">\n            <form data-bind=\"submit: upload.submit\">\n                <div class=\"form-group\">\n                    <label for=\"directory-file\">File</label>\n                    <input type=\"file\" id=\"directory-file\"\n                        placeholder=\"Your file\" required=\"true\">\n                </div>\n                <div class=\"form-group\">\n                    <div class=\"progress\">\n                        <div data-bind=\"attr: { 'aria-valuenow': upload.progress }, style: { width: upload.progress + '%' }\"\n                            class=\"progress-bar\" role=\"progressbar\" aria-valuenow=\"0\"\n                            aria-valuemin=\"0\" aria-valuemax=\"100\" style=\"width: 0%\">\n                            <span data-bind=\"text: upload.progress + '% complete'\" class=\"sr-only\"></span>\n                        </div>\n                    </div>\n                </div>\n                <button type=\"submit\" class=\"btn btn-default\">\n                    <span class=\"glyphicon glyphicon-upload\"></span> Upload\n                </button>\n                <a data-bind=\"click: upload.cancel\" class=\"btn btn-default\" href=\"#\">\n                    <span class=\"glyphicon glyphicon-remove-circle\"></span> Cancel\n                </a>\n            </form>\n        </div>\n    </div>\n<!-- /ko -->\n\n<div data-bind=\"foreach: entries\" class=\"list-group\">\n    <a data-bind=\"attr: { href: (directory ? '#directory/' : '#file/') + encoded }\" href=\"#\" class=\"list-group-item\">\n        <span data-bind=\"css: { 'glyphicon-folder-close': directory, 'glyphicon-file': !directory }\" class=\"glyphicon\"></span>\n        <span data-bind=\"text: name\"></span>\n    </a>\n</div>\n"
});
